import React from "react";
import { Box, ButtonBase, Dialog, Stack, Typography } from "@mui/material";
import { Close } from "@mui/icons-material";
import useMediaQuery from "@mui/material/useMediaQuery";

export default function MobileModal({ open, data, onClose }) {
  const isMobile = useMediaQuery("(max-width:468px)");
  return (
    <Dialog
      open={open}
      onClose={onClose}
      sx={{
        ".MuiPaper-root": { borderRadius: 0, bgcolor: data.color },
      }}
      fullScreen={isMobile}
      scroll="body"
    >
      <Box sx={{ overflow: "hidden" }}>
        <Stack direction="row">
          <Box sx={{ py: 2, flex: 1, border: "1px solid #000" }}></Box>
          <ButtonBase
            sx={{ width: 36, border: "1px solid #000" }}
            onClick={onClose}
          >
            <Close />
          </ButtonBase>
        </Stack>
        <Box sx={{ p: 1, border: "1px solid #000" }}>
          <Typography
            variant="h6"
            fontSize={{ xs: 42, sm: 60 }}
            fontWeight="bold"
            fontFamily="Anton"
            textAlign="center"
          >
            {data.title}
          </Typography>
        </Box>
        <Box sx={{ p: 1, border: "1px solid #000" }}>
          <Typography textAlign="center" textTransform="uppercase">
            {data.slogan}
          </Typography>
        </Box>
        <Box
          flex={1}
          p={1}
          sx={{
            maxHeight: isMobile ? "auto" : 360,
            overflowY: isMobile ? "auto" : "scroll",
            border: "1px solid #000",
            "::-webkit-scrollbar-track": {
              backgroundColor: data.color,
              borderBottom: "2px solid #000",
            },
          }}
        >
          <Typography
            fontSize={12}
            fontWeight="bold"
            textAlign="justify"
            dangerouslySetInnerHTML={{ __html: data.description }}
          />
        </Box>
        <Stack direction="row">
          <Box
            sx={{
              flex: 1,
              display: "flex",
              alignItems: "flex-end",
              justifyContent: "flex-end",
              border: "1px solid #000",
              position: "relative",
            }}
          >
            <Typography
              variant="h6"
              fontSize={{ xs: 38, sm: 48 }}
              lineHeight={1}
              m={2}
            >
              *0{data.serial}
            </Typography>
          </Box>
          <Box
            sx={{
              flex: 1,
              display: "flex",
              justifyContent: "center",
              alignItems: "center",
              bgcolor: "#000",
              "&> video": { margin: 0, padding: 0, width: 300 },
            }}
          >
            <video
              style={{
                transform: "translateX(1px)",
                border: "1px solid #000",
              }}
              muted
              autoPlay
              loop
            >
              <source src={`/${data.id}.mp4`} type="video/mp4" />
            </video>
          </Box>
        </Stack>
      </Box>
    </Dialog>
  );
}
