import * as React from "react";
import Button from "@mui/material/Button";
import Close from "@mui/icons-material/Close";
import Drawer from "@mui/material/Drawer";
import IconButton from "@mui/material/IconButton";
import Stack from "@mui/material/Stack";
import { Collapse } from "@mui/material";
import { ExpandLess, ExpandMore } from "@mui/icons-material";
import { StaticImage } from "gatsby-plugin-image";

import discord from "../images/dw.png";
import insta from "../images/iw.png";
import twitter from "../images/tw.png";

const pages = [
  "Home",
  "The World",
  "Sneak Peeks",
  "Map",
  "Story",
  "Roadmap",
  "The Team",
  "FC",
  "FAQ",
];

export default function MenuDrawer({ open, onClose }) {
  const [expanded, setExpanded] = React.useState(false);
  return (
    <div>
      <Drawer anchor="left" open={open} onClose={onClose}>
        <Stack className="drawer" position="relative">
          <Stack alignItems="flex-end">
            <IconButton
              size="large"
              sx={{ height: "50px", width: "50px", m: 1 }}
              onClick={onClose}
            >
              <Close sx={{ color: "#fff", fontSize: "2.5rem" }} />
            </IconButton>
          </Stack>
          <Stack direction="column" alignItems="center" width="100%">
            <div
              style={{
                marginBottom: 5,
                flexGrow: 1,
                borderBottom: "2px solid #fff",
                width: "100%",
              }}
            />
            <StaticImage
              src="../images/footer_logo.png"
              width={250}
              alt="Apprentice Footer"
            />
            <div
              style={{
                marginTop: 5,
                flexGrow: 1,
                borderTop: "2px solid #fff",
                width: "100%",
              }}
            />
            <Stack
              spacing={1}
              direction="column"
              alignItems="center"
              flexWrap="wrap"
              my={2}
            >
              {pages.map((page) =>
                page === "FC" ? (
                  <React.Fragment key={page}>
                    <Button
                      sx={{
                        color: "#fff",
                        fontSize: 22,
                        my: "5px",
                      }}
                      onClick={(e) => setExpanded(!expanded)}
                      endIcon={expanded ? <ExpandLess /> : <ExpandMore />}
                    >
                      Founders Card
                    </Button>
                    <Collapse in={expanded} mountOnEnter unmountOnExit>
                      <Stack>
                        <a href="https://www.jpg.store/collection/apprenticesustrafounderscard">
                          <Button
                            sx={{ color: "#fff" }}
                            fullWidth
                            disableElevation
                          >
                            USTRA FOUNDERS CARD
                          </Button>
                        </a>
                        <a href="https://www.jpg.store/collection/apprenticesfoundersvault">
                          <Button
                            sx={{ color: "#fff" }}
                            fullWidth
                            disableElevation
                          >
                            USTRA FOUNDERS VAULT
                          </Button>
                        </a>
                      </Stack>
                    </Collapse>
                  </React.Fragment>
                ) : (
                  <a
                    key={page}
                    href={"#" + page.toLowerCase().replace(/ /g, "_")}
                    onClick={onClose}
                  >
                    <Button
                      sx={{
                        color: "#fff",
                        display: "block",
                        fontSize: 22,
                        my: "5px",
                      }}
                    >
                      {page}
                    </Button>
                  </a>
                )
              )}
            </Stack>
            <Stack
              direction="row"
              alignItems="center"
              sx={{
                "& img": { width: "auto", height: "23px" },
              }}
            >
              <a href="https://instagram.com/apprentices.cnft">
                <IconButton>
                  <img src={insta} alt="Instagram" />
                </IconButton>
              </a>
              <a href="https://twitter.com/apprenticesCNFT">
                <IconButton>
                  <img src={twitter} alt="Twitter" />
                </IconButton>
              </a>
              <a href="https://discord.gg/apprentices">
                <IconButton>
                  <img src={discord} alt="Discord" />
                </IconButton>
              </a>
            </Stack>
          </Stack>
        </Stack>
      </Drawer>
    </div>
  );
}
