import React from "react";
import districts from "../images/districts.svg";
import districtsM from "../images/districtsM.svg";
import { ReactSVG } from "react-svg";
import Box from "@mui/material/Box";
import useMediaQuery from "@mui/material/useMediaQuery";
import datos from "../data/districts";
import DesktopModal from "./DesktopModal";
import MobileModal from "./MobileModal";

const ids = [
  "downtown",
  "sumak",
  "yishu",
  "kernel",
  "oxala",
  "martins-south",
  "martins-north",
  "arrival",
  "purpura",
];

export default function Districts() {
  const [selected, setSelected] = React.useState(null);
  const isDesktop = useMediaQuery("(min-width:890px)");

  function handleSVGEvents(svg) {
    svg.addEventListener("click", function (e) {
      let parent = e.target.parentNode;
      if (!parent) return;
      let id = parent.getAttribute("id");
      if (!id) {
        parent = parent.parentNode;
        if (!parent) return;
        id = parent.getAttribute("id");
        if (!id) return;
      }
      const _selected = ids.find((i) => id.includes(i));
      if (datos[_selected]) setSelected(datos[_selected]);
    });
  }

  function handleModalClose() {
    setSelected(null);
  }

  const modalOpen = Boolean(selected);

  return (
    <>
      <Box
        id="map"
        sx={{
          background: "#000",
          p: 2,
          "& div:first-child": {
            maxWidth: 1800,
            margin: "0 auto",
          },
        }}
      >
        {isDesktop ? (
          <ReactSVG src={districts} afterInjection={handleSVGEvents} />
        ) : (
          <ReactSVG src={districtsM} afterInjection={handleSVGEvents} />
        )}
      </Box>
      {selected !== null && (
        <>
          {isDesktop ? (
            <DesktopModal
              open={modalOpen}
              onClose={handleModalClose}
              data={selected}
            />
          ) : (
            <MobileModal
              open={modalOpen}
              onClose={handleModalClose}
              data={selected}
            />
          )}
        </>
      )}
    </>
  );
}
