export default function FAQ() {
  const [expanded, setExpanded] = useState(false);
  const handleChange = (panel) => (event, isExpanded) => {
    setExpanded(isExpanded ? panel : false);
  };
  return (
    <Stack
      alignItems="center"
      sx={{
        position: "relative",
        minHeight: "700px",
        bgcolor: "#111",
        py: 6,
        overflow: "hidden",
      }}
      className="the-faq"
      id="faq"
    >
      <Container maxWidth="sm" sx={{ px: { xs: 1, sm: 2 } }}>
        <Typography
          variant="subtitle1"
          color="white"
          fontFamily="Roboto Mono"
          fontSize={23}
        >
          06.
        </Typography>
        <Typography color="#fff" variant="h1" component="div" gutterBottom>
          FAQs
        </Typography>
        {faqs.map((faq, index) => (
          <Fragment key={index}>
            <Accordion
              expanded={expanded === index}
              onChange={handleChange(index)}
              sx={{
                bgcolor: "#111",
                borderRadius: 0,
                boxShadow: "none",
                color: "#fff",
                mt: 1,
              }}
            >
              <AccordionSummary
                expandIcon={
                  expanded !== index ? (
                    <ExpandIcon
                      sx={{
                        color: "#fff",
                      }}
                    />
                  ) : (
                    <RemoveIcon
                      sx={{
                        color: "#fff",
                      }}
                    />
                  )
                }
              >
                <Typography variant="h2">
                  <Typography
                    variant="h2"
                    component="span"
                    color={expanded !== index ? "#ed45f1" : "lime"}
                    mr={1}
                  >
                    {String(index + 1).padStart(2, "0")}.
                  </Typography>
                  {faq.question}
                </Typography>
              </AccordionSummary>
              <AccordionDetails>
                <Typography>
                  <span
                    dangerouslySetInnerHTML={{ __html: faq.answer || "TBA" }}
                  />
                </Typography>
              </AccordionDetails>
            </Accordion>
            <Divider sx={{ borderColor: "white", mt: 1 }} />
          </Fragment>
        ))}
      </Container>
    </Stack>
  );
}
