import AppBar from "@mui/material/AppBar";
import Box from "@mui/material/Box";
import Button from "@mui/material/Button";
import Container from "@mui/material/Container";
import IconButton from "@mui/material/IconButton";
import MenuIcon from "@mui/icons-material/Menu";
import Stack from "@mui/material/Stack";
import Toolbar from "@mui/material/Toolbar";
import useMediaQuery from "@mui/material/useMediaQuery";
import useScrollTrigger from "@mui/material/useScrollTrigger";
import React, { cloneElement, useState } from "react";
import { Popover } from "@mui/material";

import MenuDrawer from "./MenuDrawer";
import discord from "../images/discord.png";
import insta from "../images/insta.png";
import logo from "../images/logo.gif";
import twitter from "../images/twitter.png";

function ElevationScroll({ children }) {
  const trigger = useScrollTrigger({
    disableHysteresis: true,
    threshold: 0,
  });
  return cloneElement(children, {
    elevation: trigger ? 2 : 0,
  });
}

const pages = [
  "Home",
  "The World",
  "Sneak Peeks",
  "Map",
  "Story",
  "Roadmap",
  "The Team",
  "FC",
  "FAQ",
];

const MainAppBar = () => {
  const [drawerOpened, setDrawerOpened] = useState(false);
  const [anchorEl, setAnchorEl] = useState(null);

  const isMobile = useMediaQuery("(max-width:899px)");

  const handleOpenDrawer = () => {
    setDrawerOpened(true);
  };

  const handleCloseDrawer = () => {
    setDrawerOpened(false);
  };

  return (
    <>
      <ElevationScroll>
        <AppBar
          sx={{ bgcolor: "#fff", color: "#333", maxWidth: "100vw" }}
          position={isMobile ? "fixed" : "sticky"}
        >
          <Container maxWidth="xl">
            <Toolbar disableGutters>
              <Stack
                sx={{
                  mr: 1,
                  display: "flex",
                  "@media (max-width: 1111px)": {
                    display: "none",
                  },
                  "& img": { height: "100px", width: "auto" },
                }}
              >
                <img src={logo} alt="Apprentice Logo" />
              </Stack>

              <Box
                sx={{
                  flexGrow: 1,
                  display: "none",
                  "@media (max-width: 1111px)": {
                    display: "block",
                  },
                }}
              >
                <IconButton
                  size="large"
                  onClick={handleOpenDrawer}
                  color="inherit"
                >
                  <MenuIcon />
                </IconButton>
                <MenuDrawer open={drawerOpened} onClose={handleCloseDrawer} />
              </Box>
              <Stack
                sx={{
                  mx: 1,
                  display: "none",
                  "@media (max-width: 1111px)": {
                    display: "block",
                  },
                  "& img": { height: "auto", width: "100px" },
                }}
              >
                <img src={logo} alt="Apprentice Logo" />
              </Stack>
              <div style={{ flexGrow: 1 }} />
              <Stack
                spacing="5px"
                direction="row"
                alignItems="center"
                sx={{
                  display: "flex",
                  "@media (max-width: 1111px)": {
                    display: "none",
                  },
                  "& .MuiButton-root": {
                    borderBottom: "2px solid #fff",
                    transition: "border-color 0.2s ease-in-out",
                    minWidth: "auto",
                  },
                  "& .MuiButton-root:hover": {
                    borderColor: "#333",
                  },
                }}
              >
                {pages.map((page) =>
                  page === "FC" ? (
                    <Button
                      key={page}
                      sx={{
                        my: 2,
                        color: "#333",
                        display: "block",
                      }}
                      onClick={(e) => setAnchorEl(e.currentTarget)}
                    >
                      Founders Card
                    </Button>
                  ) : (
                    <a
                      key={page}
                      href={"#" + page.toLowerCase().replace(/ /g, "_")}
                    >
                      <Button
                        sx={{
                          my: 2,
                          color: "#333",
                          display: "block",
                        }}
                      >
                        {page}
                      </Button>
                    </a>
                  )
                )}
              </Stack>
              <Stack
                direction="row"
                alignItems="center"
                sx={{
                  "& img": {
                    width: "auto",
                    height: "23px",
                    display: { xs: "none", md: "flex" },
                  },
                  ml: 2,
                }}
              >
                <a href="https://instagram.com/apprentices.cnft">
                  <IconButton>
                    <img src={insta} alt="Instagram" />
                  </IconButton>
                </a>
                <a href="https://twitter.com/apprenticesCNFT">
                  <IconButton>
                    <img src={twitter} alt="Twitter" />
                  </IconButton>
                </a>
                <a href="https://discord.gg/apprentices">
                  <IconButton>
                    <img src={discord} alt="Discord" />
                  </IconButton>
                </a>
              </Stack>
            </Toolbar>
          </Container>
        </AppBar>
      </ElevationScroll>
      <Popover
        open={Boolean(anchorEl)}
        anchorEl={anchorEl}
        onClose={() => setAnchorEl(null)}
        anchorOrigin={{
          vertical: "bottom",
          horizontal: "left",
        }}
      >
        <a href="https://www.jpg.store/collection/apprenticesustrafounderscard">
          <Button sx={{ color: "#333", display: "block" }}>
            USTRA FOUNDERS CARD
          </Button>
        </a>
        <a href="https://www.jpg.store/collection/apprenticesfoundersvault">
          <Button sx={{ color: "#333", display: "block" }}>
            USTRA FOUNDERS VAULT
          </Button>
        </a>
      </Popover>
    </>
  );
};
export default MainAppBar;
