import Button from "@mui/material/Button";
import Close from "@mui/icons-material/Close";
import Container from "@mui/material/Container";
import Dialog from "@mui/material/Dialog";
import DialogContent from "@mui/material/DialogContent";
import DialogTitle from "@mui/material/DialogTitle";
import Slide from "@mui/material/Slide";
import Stack from "@mui/material/Stack";
import Typography from "@mui/material/Typography";
import React, { forwardRef, useState } from "react";
import { StaticImage } from "gatsby-plugin-image";

import RaceCard from "./RaceCard";

const Transition = forwardRef(function Transition(props, ref) {
  return <Slide direction="up" ref={ref} {...props} />;
});

export default function TheRaces() {
  const [open, setOpen] = useState(false);

  const handleOpen = () => {
    setOpen(true);
  };

  const handleClose = () => {
    setOpen(false);
  };

  return (
    <div>
      <Stack mt={3} alignItems={["center", "flex-start"]}>
        <Button
          variant="outlined"
          sx={{
            py: 1,
            color: "#fff",
            borderColor: "#fff",
            borderRadius: 0,
            width: 240,
            bgcolor: "rgb(0,0,0)",
            "&:hover": { borderColor: "#ccc", bgcolor: "rgba(0,0,0,0.05)" },
          }}
          onClick={handleOpen}
        >
          EXPLORE THE RACES
        </Button>
      </Stack>
      <Dialog
        open={open}
        TransitionComponent={Transition}
        keepMounted
        onClose={handleClose}
        fullScreen
      >
        <DialogTitle
          sx={{
            fontSize: 20,
            pl: 4,
            py: 0,
            pr: 0,
            borderBottom: "2px solid #333",
            display: "flex",
            alignItems: "center",
            justifyContent: "space-between",
          }}
        >
          <Typography fontWeight="bold" variant="body2" fontSize={22}>
            EXPLORE THE RACES
          </Typography>
          <Button
            size="large"
            sx={{
              borderLeft: "2px solid #333",
              p: { xs: 2, sm: 3, md: 4 },
            }}
            onClick={handleClose}
          >
            <Close />
          </Button>
        </DialogTitle>
        <DialogContent id="sb" sx={{ p: 0 }}>
          <Container maxWidth="lg">
            <RaceCard
              label="HUMANS"
              image1={
                <StaticImage
                  src="../images/human.png"
                  width={122}
                  height={123}
                  style={{ outline: "1px solid #ccc" }}
                  alt="human"
                />
              }
              image2={
                <StaticImage
                  src="../images/human-male.png"
                  width={122}
                  height={123}
                  style={{ outline: "1px solid #ccc" }}
                  alt="human"
                />
              }
              description={
                "Following the great cataclysm of 2037 A.D and the 33 Year War, humanity was on the verge of extinction. Despite this, the world managed to rebuild itself under the banner of 6 new global alliances: The Republic of America, The European Alliance, The African Kingdom, The Asian Nation and the Ocean Empire.<br/><br/>After years of negotiation these alliances forged a peace treaty which started a new era of swift and steady development on Earth. This led to discoveries in the quantum fields of science and new ways of interpreting the metaphysical world. During this time certain humans began to show signs of abilities, once thought to be impossible to use. Telekinesis, remote-viewing, levitation… all these once forgotten techniques were reborn. This became more common with the passing of decades."
              }
            />
            <RaceCard
              label="ANDROIDS"
              image1={
                <StaticImage
                  src="../images/android.png"
                  width={122}
                  height={123}
                  style={{ outline: "1px solid #ccc" }}
                  alt="android"
                />
              }
              image2={
                <StaticImage
                  src="../images/android-male.png"
                  width={122}
                  height={123}
                  style={{ outline: "1px solid #ccc" }}
                  alt="android"
                />
              }
              description={
                'Humans that mastered the comprehension of quantum laws took technology to previously unimaginable levels. A new life form emerged.<br/><br/>They are the "children" of humans and have been operating as a nation for more than 150 years under the laws of the maximum director, an AI in charge of the "womb", a sacred place where the memory of their species is stored and to which all can connect in order to communicate. The maximum director is elected by androids from all over the world, similar to a democratic system.<br/><br/>Androids are basically independent AIs that have developed to the point of creating consciousness within the space of electrical impulses.'
              }
            />
            <RaceCard
              label="ARCTURIANS"
              image1={
                <StaticImage
                  src="../images/arcturian.png"
                  width={122}
                  height={123}
                  style={{ outline: "1px solid #ccc" }}
                  alt="arcturian"
                />
              }
              image2={
                <StaticImage
                  src="../images/arcturian-male.png"
                  width={122}
                  height={123}
                  style={{ outline: "1px solid #ccc" }}
                  alt="arcturian"
                />
              }
              description={
                "Arcturians are one of the most evolved beings in the galaxy and the final piece that completes the trinity of the three races.<br/><br/>They arrived at Planet Earth in the year 2280 after the great migration from the Bootes constellation and adapted to its way of living in the third dimension. They all manifest strong metaphysic powers like telekinesis, foresight, remote viewing, telepathy, matter manipulation, and even the summoning of entities.<br/><br/>Their mission objective is yet a mystery, but one thing is certain, their love for humanity and its customs."
              }
            />
            <Stack mb={4} width="100%" alignItems="center">
              <Button startIcon={<Close />} onClick={handleClose}>
                CLOSE
              </Button>
            </Stack>
          </Container>
        </DialogContent>
      </Dialog>
    </div>
  );
}
