import Box from "@mui/material/Box";
import ButtonBase from "@mui/material/ButtonBase";
import Stack from "@mui/material/Stack";
import Typography from "@mui/material/Typography";
import React, { useEffect, useRef, useState } from "react";
import { StaticImage } from "gatsby-plugin-image";

import MainAppBar from "./MainAppBar";
import desktop from "../images/desktop.mp4";
import logo from "../images/logo.gif";
import mobile from "../images/mobile.mp4";

export default function Home() {
  const videoRef = useRef(null);
  const [muted, setMuted] = useState(true);
  const [height, setHeight] = useState(1080);
  const [showScroller, setShowScroller] = useState(true);

  useEffect(() => {
    const calcHeight = () => {
      const h = Math.min(1080, document.body.clientWidth * 0.5625);
      setHeight(h);
      setShowScroller(h > window.innerHeight);
    };
    window.addEventListener("resize", calcHeight);
    calcHeight();
  }, []);

  useEffect(() => {
    if (videoRef.current?.paused) {
      videoRef.current.play();
    }
  }, [muted]);

  function toggleMuted() {
    videoRef.current.muted = !muted;
    setMuted((m) => !m);
  }

  return (
    <>
      <Box className="home-video" style={{ height }}>
        <video ref={videoRef} autoPlay loop muted playsInline>
          <source src={desktop} type="video/mp4" />
          <source src={mobile} type="video/mp4" />
        </video>
        {showScroller && (
          <div className="scroll-down">
            <div className="chevron"></div>
            <div className="chevron"></div>
            <div className="chevron"></div>
            <span>Scroll down</span>
          </div>
        )}
      </Box>
      <ButtonBase
        id="bars"
        className={muted ? "" : "unmuted"}
        sx={{
          left: 30,
          top: height - 50,
          "@media (max-width:899px)": {
            top: 70,
            left: 6,
            transform: "scaleY(-1)",
          },
          "@media (max-width:599px)": { top: 60, left: 5 },
        }}
        onClick={toggleMuted}
      >
        <div className="bar"></div>
        <div className="bar"></div>
        <div className="bar"></div>
        <div className="bar"></div>
        <div className="bar"></div>
        <div className="bar"></div>
        <div className="bar"></div>
        <div className="bar"></div>
        <div className="bar"></div>
        <div className="bar"></div>
      </ButtonBase>
      <MainAppBar />
      <Stack alignItems="center" id="home" position="relative">
        <Box className="home-hero">
          <Box className="hero-image">
            <StaticImage
              src="../images/hero_1.png"
              alt="Apprentice Model 1"
              placeholder="blurred"
            />
          </Box>
          <Box
            width="100%"
            display="flex"
            justifyContent="center"
            alignItems="center"
            className="hero-logo"
            sx={{
              mt: [0, 1],
              mb: 3,
              "& img": {
                p: "5px",
                width: "100%",
                maxWidth: 800,
              },
            }}
          >
            <img src={logo} alt="Apprentice Logo" />
          </Box>
          <Typography textAlign="center" p={1}>
            We are on the eve of the 24th century. Humanity has helped AI expand
            their consciousness and alien civilizations from other constellation
            have arrived on Planet Earth, which finds itself in a brief period
            of prosperity.
            <br />
            <br />
            How will this alliance help shape the future of the universe?
            <br />A group of Apprentices is ready to show they can evolve.
          </Typography>
        </Box>
      </Stack>
    </>
  );
}
