import CloseIcon from "@mui/icons-material/Close";
import Container from "@mui/material/Container";
import Dialog from "@mui/material/Dialog";
import DialogContent from "@mui/material/DialogContent";
import IconButton from "@mui/material/IconButton";
import Typography from "@mui/material/Typography";
import React, { useState } from "react";
import { CheckCircle } from "@mui/icons-material";
import { ReactSVG } from "react-svg";
import { Stack } from "@mui/material";

import roadmap from "../images/roadmap.svg";
import roadmap1 from "../images/roadmap1.svg";

const cns = ["Q1", "Q2", "Q3", "Q4", "Q1Q2"];

const cns_data = {
  Q1: {
    header: "Q1 – 2023",
    items: [
      "Character design and concept.",
      "Organic growth.",
      "Weekly sneak-peeks.",
      "Community discord server opening, Apprentices gather.",
      "Early active & supportive members of the community will be granted the OG status.",
    ],
    checked: true,
  },
  Q2: {
    header: "Q2 – 2023",
    items: [
      "Continue working on the crafting of the collection.",
      "Hosting Apprentices assembly X (Twitter) spaces.",
    ],
    checked: true,
  },
  Q3: {
    header: "Q3 – 2023",
    items: [
      "1/1 sneak-peek reveals.",
      "Listing on all major marketplaces.",
      "Pilot Episode Teaser reveal.",
      "Whitelist for pre-sale.",
      "Launch releasing Apprentices to the Cardano blockchain.",
    ],
  },
  Q4: {
    header: "Q4 – 2023",
    items: [
      "Release of Chapter 3 of the light novel in our website.",
      "Listing on all major marketplaces.",
      "Rarity rank listing on <a href='https://cnft.tools/' target='_blank'>CNFT.tools</a> & <a href='https://www.jngl.io/' target='_blank'>CNFTJungle.io</a>",
      "First art collaboration.",
      "First airdrop for Apprentices holders.",
      "Initiate the manga production.",
      "Writing of the script for storyboard of the pilot episode.",
    ],
  },
  Q1Q2: {
    header: "Q1/Q2 – 2024",
    items: [
      "Presentation of Apprentices metaverse quarters at Pavia & Clay Nation.",
      "Third airdrop for Apprentices holders.",
      "Mysterious event!",
      "More to come…",
    ],
  },
};
export default function RoadMap() {
  const [text, setText] = useState(null);
  function handleSVGEvents(_, svg) {
    svg.addEventListener("click", function (e) {
      const className = e.target.getAttribute("class");
      const cn = (className || "").split(" ")[0];
      if (cns.includes(cn)) {
        setText(cns_data[cn]);
      } else {
        setText(null);
      }
    });
  }
  return (
    <Container maxWidth="lg" id="roadmap">
      <Typography
        variant="subtitle1"
        color="#333"
        fontFamily="Roboto Mono"
        fontSize={23}
      >
        04.
      </Typography>
      <Typography
        color="#333"
        textTransform="uppercase"
        variant="h1"
        component="div"
        gutterBottom
      >
        ROADMAP
      </Typography>
      <Typography color="#333" mb={4}>
        There are no shortcuts.
        <br />
        <br />
        After launch Roadmap V2.0 will be published.
      </Typography>
      <div className="roadmap-svg">
        <ReactSVG
          src={roadmap}
          afterInjection={handleSVGEvents}
          className="desktop"
        />
        <ReactSVG
          src={roadmap1}
          afterInjection={handleSVGEvents}
          className="mobile"
        />
      </div>
      <Dialog open={text !== null} onClose={() => setText(null)} maxWidth="xs">
        <DialogContent>
          <IconButton
            aria-label="close"
            onClick={() => setText(null)}
            sx={{
              position: "absolute",
              right: 8,
              top: 8,
              color: "grey",
            }}
          >
            <CloseIcon />
          </IconButton>
          {text !== null && (
            <>
              <Typography fontWeight="bold" mb={3}>
                {text.header}
              </Typography>
              <Stack spacing={1}>
                {text.items.map((item, index) => (
                  <Stack direction="row" alignItems="center" key={index}>
                    <Typography>
                      –
                      <span
                        className="dl-txt"
                        dangerouslySetInnerHTML={{ __html: item }}
                      />
                    </Typography>
                    {text?.checked && (
                      <CheckCircle color="success" fontSize="1rem" />
                    )}
                  </Stack>
                ))}
              </Stack>
            </>
          )}
        </DialogContent>
      </Dialog>
    </Container>
  );
}
