import Slider from "@mui/material/Slider";
import Stack from "@mui/material/Stack";
import Typography from "@mui/material/Typography";
import React, { useState } from "react";
import { Splide, SplideSlide, SplideTrack } from "@splidejs/react-splide";

import hero_1 from "../images/hero_3.png";
import hero_2 from "../images/hero_4.png";
import hero_3 from "../images/hero_5.png";
import hero_4 from "../images/hero_6.jpg";
import hero_5 from "../images/hero_7.jpg";
import hero_6 from "../images/hero_8.jpg";
import hero_7 from "../images/hero_9.jpg";
import hero_8 from "../images/shiro-ehak.jpg";

const heros = [hero_1, hero_2, hero_3, hero_4, hero_5, hero_6, hero_7, hero_8];

export default function SneakPeeks() {
  const [progress, setProgress] = useState(0);
  function handleSlideChange(splide) {
    const end = splide.Components.Controller.getEnd() + 1;
    setProgress((100 * (splide.index + 1)) / end);
  }
  return (
    <Stack
      sx={{
        position: "relative",
        minHeight: "700px",
        py: 6,
        px: 1,
        // ":before": {
        //   content: "''",
        //   position: "absolute",
        //   bottom: 0,
        //   left: 0,
        //   borderTop: "120px solid transparent",
        //   borderLeft: "120px solid #000",
        //   display: { xs: "none", md: "block" },
        // },
      }}
      id="sneak_peeks"
    >
      <Stack maxWidth="600px" mx="auto">
        <Typography
          variant="subtitle1"
          color="#333"
          fontFamily="Roboto Mono"
          fontSize={23}
        >
          02.
        </Typography>
        <Typography
          color="#333"
          textTransform="uppercase"
          variant="h1"
          component="div"
          gutterBottom
        >
          Sneak Peeks
        </Typography>
        <Typography color="#333" pr={1}>
          Meet some of the Apprentices, each one with their own vibe. Find yours
          and write a new chapter in their story.
        </Typography>
        <Stack position="relative" height={480} width="100vw" overflow="hidden">
          <Stack position="absolute" height={480} width={1080}>
            <Splide
              hasTrack={false}
              options={{
                perMove: 1,
                arrows: false,
                autoplay: true,
                rewind: true,
                type: "loop",
                draggable: false,
                pagination: false,
                gap: 60,
                perPage: 3,
                breakpoints: {
                  600: {
                    gap: 20,
                  },
                },
              }}
              onMove={handleSlideChange}
              onMounted={handleSlideChange}
            >
              <Stack my={3} direction="row" justifyContent="flex-start">
                <SplideTrack>
                  {heros.map((hero, i) => (
                    <SplideSlide key={i}>
                      <Stack
                        width={300}
                        p={2}
                        border="1px solid #ccc"
                        position="relative"
                        sx={{
                          "& img": {
                            width: 268,
                            height: 268,
                          },
                        }}
                        bgcolor="#fff"
                      >
                        <Stack
                          sx={{
                            width: "1px",
                            height: "1px",
                            position: "absolute",
                            bottom: 0,
                            left: 0,
                            bgcolor: "#fff",
                            borderTop: "80px solid #fff",
                            borderLeft: "80px solid transparent",
                          }}
                        />
                        <img src={hero} alt="Apprentice" />
                        <Typography my={2} textAlign="right">
                          <strong>Apprentice #????</strong>
                        </Typography>
                        <Typography textAlign="right" variant="subtitle2">
                          Name: #????
                        </Typography>
                        <Typography textAlign="right" variant="subtitle2">
                          Gift: #????
                        </Typography>
                      </Stack>
                    </SplideSlide>
                  ))}
                </SplideTrack>
              </Stack>
              <Slider
                value={progress}
                size="large"
                step={12}
                sx={{
                  maxWidth: "calc(100vw - 16px)",
                  pointerEvents: "none",
                  "& .MuiSlider-thumb": { display: "none" },
                  "& .MuiSlider-track": {
                    bgcolor: "#333",
                    borderRadius: 0,
                    height: 10,
                    borderColor: "transparent",
                  },
                  "& .MuiSlider-rail": {
                    bgcolor: "#fff",
                    borderTop: "2px solid #000",
                    borderBottom: "2px solid #000",
                    borderRight: "2px solid #000",
                    borderRadius: 0,
                    height: 8,
                  },
                }}
              />
            </Splide>
          </Stack>
        </Stack>
      </Stack>
    </Stack>
  );
}
