import Button from "@mui/material/Button";
import IconButton from "@mui/material/IconButton";
import React from "react";
import Stack from "@mui/material/Stack";
import Typography from "@mui/material/Typography";
import { Collapse } from "@mui/material";
import { ExpandLess, ExpandMore } from "@mui/icons-material";
import { StaticImage } from "gatsby-plugin-image";

import discord from "../images/dw.png";
import insta from "../images/iw.png";
import twitter from "../images/tw.png";

const pages = [
  "Home",
  "The World",
  "Sneak Peeks",
  "Map",
  "Story",
  "Roadmap",
  "The Team",
  "FC",
  "FAQ",
];

export default function Footer() {
  const [expanded, setExpanded] = React.useState(false);
  React.useEffect(() => {
    const styles =
      "color:#f79522;background:#101010;font-size:20px;font-weight:bold;border:3px solid #f79522;padding:10px 20px;border-radius:30px;margin:20px 0";
    console.info("%cDeveloped with ♥ by https://shakil.info", styles);
  }, []);
  return (
    <Stack className="footer" justifyContent="center" position="relative">
      <Stack
        direction={["column", "row"]}
        alignItems="center"
        width="100%"
        p={2}
        sx={{
          borderTop: { sm: "none", md: "1px solid #fff" },
          borderBottom: { sm: "none", md: "1px solid #fff" },
          "& .gatsby-image-wrapper": { display: { xs: "none", md: "block" } },
        }}
      >
        <div style={{ flexGrow: 1 }} />
        <StaticImage
          src="../images/footer_logo.png"
          height={50}
          alt="Apprentice Footer"
        />
        <div style={{ flexGrow: 1 }} />
        <Stack
          spacing={1}
          direction={["column", "row"]}
          alignItems="center"
          flexWrap="wrap"
        >
          {pages.map((page) =>
            page === "FC" ? (
              <React.Fragment key={page}>
                <Button
                  sx={{
                    color: "#fff",
                    my: { xs: 1, md: 2 },
                  }}
                  onClick={(e) => setExpanded(!expanded)}
                  endIcon={expanded ? <ExpandLess /> : <ExpandMore />}
                >
                  Founders Card
                </Button>
                <Collapse in={expanded} mountOnEnter unmountOnExit>
                  <Stack>
                    <a href="https://www.jpg.store/collection/apprenticesustrafounderscard">
                      <Button sx={{ color: "#fff" }} fullWidth disableElevation>
                        USTRA FOUNDERS CARD
                      </Button>
                    </a>
                    <a href="https://www.jpg.store/collection/apprenticesfoundersvault">
                      <Button sx={{ color: "#fff" }} fullWidth disableElevation>
                        USTRA FOUNDERS VAULT
                      </Button>
                    </a>
                  </Stack>
                </Collapse>
              </React.Fragment>
            ) : (
              <a key={page} href={"#" + page.toLowerCase().replace(/ /g, "_")}>
                <Button
                  sx={{
                    my: { xs: 1, md: 2 },
                    color: "#fff",
                    display: "block",
                  }}
                >
                  {page}
                </Button>
              </a>
            )
          )}
        </Stack>
        <Stack
          direction="row"
          alignItems="center"
          mt={[2, 0]}
          sx={{
            "& img": { width: "auto", height: "23px" },
            ml: { xs: 0, md: 2 },
          }}
        >
          <a href="https://instagram.com/apprentices.cnft">
            <IconButton>
              <img src={insta} alt="Instagram" />
            </IconButton>
          </a>
          <a href="https://twitter.com/apprenticesCNFT">
            <IconButton>
              <img src={twitter} alt="Twitter" />
            </IconButton>
          </a>
          <a href="https://discord.gg/apprentices">
            <IconButton>
              <img src={discord} alt="Discord" />
            </IconButton>
          </a>
        </Stack>
        <div style={{ flexGrow: 1 }} />
      </Stack>
      <Stack
        direction="row"
        alignItems="center"
        sx={{ position: "absolute", bottom: 20, width: "100%" }}
      >
        <div style={{ flexGrow: 1 }} />
        <Typography mt={3} color="#fff">
          &copy; 2022 ALL RIGHTS RESERVED
        </Typography>
        <Stack flexGrow={[1, 4]} />
      </Stack>
    </Stack>
  );
}
